import React from 'react';import style from './ProductsContainer.module.css';import {connect} from 'react-redux';import Loader from "../Common/Loader";import {getAllProductsThunkCreator} from "../../redux/productsReducer";import Product from "./Product/Product";class ProductsContainer extends React.Component {  componentDidMount() {    this.props.getAllProductsThunkCreator()  }  render() {    return (      <div>        {!this.props.isLoaded ? <Loader/> : null}        <h1 className={style.productsTitle}>Products</h1>        <div className={style.products}>          {this.props.products.map(product => <Product title={product.title}                                                       text={product.text}                                                       key={product.id}                                                       id={product.id}                                                       img={product.img}/>)}        </div>      </div>    )  }}const mapStateToProps = (state) => {  return {    isLoaded: state.productsPage.isLoaded,    products: state.productsPage.products  }};export default connect(mapStateToProps, {getAllProductsThunkCreator})(ProductsContainer);